import { useState, useEffect } from 'react';
import useAuth from './useAuth';
import Player from './Player';
import TrackSearchResult from './TrackSearchResult';
import { Container, Form } from 'react-bootstrap';
import SpotifyWebApi from 'spotify-web-api-node';
import axios from 'axios';
import './Dashboard.css';

const spotifyApi = new SpotifyWebApi({
  clientId: '455d30aa9c0a4cefa57a5050a7b854f4',
});

export default function Dashboard({ code }) {
  const accessToken = useAuth(code);
  const [userPlaylist, setuserPlaylist] = useState('');
  const [playlistsFetched, setPlaylistsFetched] = useState(false);
  const [playlistsSelected, setPlaylistSelected] = useState('');
  const [userId, setuserId] = useState('');
  const [search, setSearch] = useState('');
  const [searchResults, setSearchResults] = useState([]);
  const [playingTrack, setPlayingTrack] = useState();
  // const [lyrics, setLyrics] = useState('');

  function choosePlaylist(id) {
    spotifyApi.getPlaylistTracks(id).then(
      function (data) {
        // console.log('The playlist contains these tracks', data.body);
        const respond = data.body;
        // console.log(respond);
        const playlistsSelected = {
          tracks: respond.items.map(({ track }) => ({
            id: track.id,
            name: track.name,
            artists: track.artists.map((artist) => artist.name),
            image: track.album.images[2].url,
            duration: track.duration_ms,
            album: track.album.name,
            context_uri: track.album.uri,
            track_number: track.track_number,
          })),
        };
        setPlaylistSelected(playlistsSelected);
        console.log('PLAYLIST SLECET', playlistsSelected);
      },
      function (err) {
        console.log('Something went wrong!', err);
      }
    );
  }

  function chooseTrack(track) {
    setPlayingTrack(track);
    setSearch('');
    // setLyrics('');
  }

  // useEffect(() => {
  //   if (!playingTrack) return;
  //   axios
  //     .get('http://localhost:3001/lyrics', {
  //       params: {
  //         track: playingTrack.title,
  //         artist: playingTrack.artist,
  //       },
  //     })
  //     .then((res) => {
  //       setLyrics(res.data.lyrics);
  //     });
  // }, [playingTrack]);

  useEffect(() => {
    if (!accessToken) return;
    spotifyApi.setAccessToken(accessToken);
  }, [accessToken]);

  useEffect(() => {
    //Get User
    if (!accessToken) return;
    spotifyApi.getMe().then(
      function (data) {
        // console.log('Some information user', data.body);
        setuserId(data.body.id);
        console.log('Id', data.body.id);
      },
      function (err) {
        console.log('Something went wrong!', err);
      }
    );
  }, [userId, accessToken]);

  useEffect(() => {
    if (!accessToken || (!userId && userPlaylist !== '') || playlistsFetched) return;

    spotifyApi.getUserPlaylists(userId).then(
      function (data) {
        console.log('Retrieved playlists', data.body);
        const { items } = data.body;
        const playlists = items.map(({ name, id }) => {
          return { name, id };
        });
        setuserPlaylist(playlists);
        setPlaylistsFetched(true);
        console.log('playlists', playlists);
      },
      function (err) {
        console.log('Something went wrong!', err);
      }
    );
  }, [userId, userPlaylist, accessToken, playlistsFetched]);

  useEffect(() => {
    if (!search) return setSearchResults([]);
    if (!accessToken) return;

    let cancel = false;
    spotifyApi.searchTracks(search).then((res) => {
      if (cancel) return;
      setSearchResults(
        res.body.tracks.items.map((track) => {
          const smallestAlbumImage = track.album.images.reduce((smallest, image) => {
            if (image.height < smallest.height) return image;
            return smallest;
          }, track.album.images[0]);

          return {
            artist: track.artists[0].name,
            title: track.name,
            uri: track.uri,
            albumUrl: smallestAlbumImage.url,
          };
        })
      );
    });

    return () => (cancel = true);
  }, [search, accessToken]);

  return (
    <Container>
      <div id="bungkus" className="d-flex flex-col">
        <div>
          TITLE
          <Form.Control type="search" placeholder="Search Songs/Artists" value={search} onChange={(e) => setSearch(e.target.value)} onClick={() => setPlaylistSelected('')} />
        </div>
        <div className="d-flex flex-grow-1 my-2 flex-row" style={{ height: '80vh' }}>
          <div className="leftMenus my-2">
            PLAYLIST
            {userPlaylist.length > 0 && (
              <ul>
                {userPlaylist.map(({ name, id }) => (
                  <li id={id} onClick={(e) => choosePlaylist(e.target.id)}>
                    {name}
                  </li>
                ))}
              </ul>
            )}
          </div>
          <div className="rightMenu flex-grow-1 my-2" style={{ overflowY: 'auto' }}>
            <div style={{ overflowY: 'auto' }}>
              <div style={{ overflowY: 'auto' }}>
                {searchResults.map((track) => (
                  <TrackSearchResult track={track} key={track.uri} chooseTrack={chooseTrack} />
                ))}
                {/* {searchResults.length === 0 && (
                  <div className="text-center" style={{ whiteSpace: 'pre' }}>
                    {lyrics}
                  </div>
                )} */}
                <div className="tracks">
                  {playlistsSelected &&
                    playlistsSelected.tracks.map(({ id, name, artists, image, album, context_uri }) => {
                      // console.log('line 172', id, name, artists, image, album, context_uri);
                      const track = {
                        albumUrl: image,
                        artist: artists[0],
                        title: name,
                        uri: context_uri,
                      };
                      // console.log('TRACK ', track);
                      return (
                        <div className="row" key={id} onClick={() => chooseTrack(track)}>
                          <div className="col detail">
                            <div className="image">
                              <img src={image} alt="track" />
                            </div>
                            <div className="info">
                              <span className="name">{name}</span>
                              <span>{artists}</span>
                            </div>
                          </div>
                          <div className="col">
                            <span>{album}</span>
                          </div>
                        </div>
                      );
                    })}
                </div>
              </div>
            </div>
          </div>
        </div>
        <div className="bottom">
          <Player accessToken={accessToken} trackUri={playingTrack?.uri} />
        </div>
      </div>
    </Container>
  );
}
